.datailWrapper{
  width: 80%;
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  justify-content: center;
  }

  .navbar-light .navbar-toggle{
    padding-right: 10px !important;
  }

  /* modal favorite */

  .modalFavorite{
    position: fixed;
    top: 10%;
    right: 3%;
    width: 30%;
   padding: 10px;
   color:white;
   border-radius:10px;
   transition: .4s;
    background: rgba(66, 66, 66, 0.8);
z-index: 10;
  }
  .hidden{
    display: none;
  }

  .favoriteClick{
cursor: pointer;
  }

  /* navlik favorite */

  .navLinkFavorite{
    position: relative;
  }
  .navLinkFavorite span{
    position: absolute;
    top: -3px;
    color:rgb(253, 7, 7);
  }
  .nabvarCollapse{
    justify-content: space-between;
  }
  .favorCard{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
  }
  .card{
    margin: 10px;
  }

  /* header */

  .headerLink{
    text-decoration: none;
    margin: 10px;
  }
  .headerLink a{
    text-decoration: none;
  }
  .btnFormControl{
    text-decoration: none;
  }
  /* loader Datail */

  .lds-ripple {
    display: inline-block;
    position: relative;
    width: 200px;
    height: 200px;
    margin-top: 20%;
  }
  .lds-ripple div {
    position: absolute;
    border: 4px solid #fff;
    opacity: 1;
    border-radius: 50%;
    background-color:#0d6efd;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
    
  }
  .lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes lds-ripple {
    0% {
      top: 150px;
      left: 150px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 300px;
      height: 300px;
      opacity: 0;
    }
  }

  @media screen and (max-width:1200px) {
   
