  }

  .favoriteClick{
cursor: pointer;
  }

  /* navlik favorite */

  .navLinkFavorite{
    position: relative;
  }
  .navLinkFavorite span{
    position: absolute;
    top: -3px;
    color:rgb(253, 7, 7);
  }
  .nabvarCollapse{
    justify-content: space-between;
  }
  .favorCard{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
  }
  .card{
    margin: 10px;
  }

  /* header */

  .headerLink{
    text-decoration: none;
    margin: 10px;
  }
  .headerLink a{
    text-decoration: none;
  }
  .btnFormControl{
    text-decoration: none;
  }
  /* loader Datail */

  .lds-ripple {
    display: inline-block;
    position: relative;
    width: 200px;
    height: 200px;
    margin-top: 20%;
  }
  .lds-ripple div {
    position: absolute;
    border: 4px solid #fff;
    opacity: 1;
    border-radius: 50%;
    background-color:#0d6efd;
    -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
            animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
    
  }
  .lds-ripple div:nth-child(2) {
    -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
  }
  @-webkit-keyframes lds-ripple {
    0% {
      top: 150px;
      left: 150px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 300px;
      height: 300px;
      opacity: 0;
    }
  }
  @keyframes lds-ripple {
    0% {
      top: 150px;
      left: 150px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 300px;
      height: 300px;
      opacity: 0;
    }
  }

