.contactWrapper{
  width: 80%;
  margin: 50px auto;
    display: flex;
    flex-wrap: wrap;
}
.contactTitle{
    width: 100%;
}
.contactForm{
    width: 60%;
}
form textarea{
    width: 100%;
    height: 200px;
    margin-bottom: 20px;
}
.contactFormInput{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.contactFormInput input{
     width: 47%;
     height: 40px;
     margin-bottom: 20px;
}
.sendFormBtn{
    width: 30%;
    border: 1px solid #FFC000;
    display: flex;align-items: center;justify-content: center;
    padding: 10px;
    transition: .6s;
    cursor: pointer;
    margin-bottom: 15px;
}
.sendFormBtn:hover{
    background: #FFC000;
}
.contactFormRight{
    width: 40%;
  
}
.contactFormRight img{
    width: 40px;
    height: 30px;
    margin-right: 10px;
    margin-left: 20px;
}
.contactEmail{
    display: flex;
}
@media screen and (max-width:900px) {
    .contactWrapper{
        flex-direction: column;
    }
    .contactForm{
        width: 100%;
    }
    .contactFormRight{
        width: 100%;
    }
    .sendFormBtn{
width: 100%;
    }
}